<template>
  <div class="Contains">
    <HomePageTop :TopTitle="TopTotle" ></HomePageTop>
    <div class="pl30 pr30 mt36 cl_666 f34">
      <p class="cl_333 mb32">感谢您使用捷配订单系统，本系统为您提供关于 PCB、SMT、元器件代购等定制加工服务，在使用前，请您仔细阅读以下内容，只有在您接受并遵守以下条款后再进行下一步操作。</p>
      <p class="cl_333 mb24">1、下单</p>
      <p class=" cl_666 mb24">（1）顾客请如实填写加工参数，测算价格只针对加工参数有效，具体订单价格以加工文件 与订单审核结果为准。</p>
      <p class=" cl_666 mb24">（2）订单审核过程需要专业人工干预，所以顾客下单后需要等待后台订单审核，审核等待 时间预计 30 分钟至一个小时。</p>
      <p class=" cl_666 mb24">（3）订单若有问题，捷配会以手机短信或语音电话通知“订单联系人”，为了确保您的交期， 请及时登录捷配会员中心或捷配微信公众号查看问题并回复.</p>
      <p class="cl_333 mb24">2、支付</p>
      <p class=" cl_666 mb24">（1）系统支持预充值、支付宝、微信或银联网银支付，为了您的下单顺畅和高效，强烈建 议您采用预充值或支付宝方式进行支付。</p>
      <p class=" cl_666 mb24">（2）顾客请在订单完成审核状态后 72 小时内完成付款操作，超过 20 天系统将自动取消该 订单。</p>
      <p class=" cl_666 mb24">（3）请勿重复提交订单和拆单。当您进行支付后，请查询订单支付状态，系统如没有返回 异常提醒，请勿急于重复提交订单，请您在服务时间拨打 400-8752-665 咨询，以免造成损 失。</p>
      <p class="cl_333 mb24">3、状态查询</p>
      <p class=" cl_666 mb24">（1）下单后支持对订单明细信息进行查询，包括加工文件、加工参数、订单计价信息等.</p>
      <p class=" cl_666 mb24">（2）订单完成付款之后将支持针对订单在制情况的进度查询，方便顾客快捷清晰了解产品 的整体加工状态.</p>
      <p class=" cl_666 mb24">（3）订单发货状态之后将支持发货物流的快速查询，方便顾客快捷直接了解货物物流流转 状态.</p>
      <p class="cl_333 mb24">4、售后服务</p>
      <p class=" cl_666 mb24">（1）每个订单我们在加工过程都会按照订单难度进行一定量的预投管理，但由于定制加工 服务受（品质、环境、材料等）客观因素影响较大，存在报废而造成订单缺数情况，需要顾 客谅解。在订单出现缺数情况下系统会发出缺数处理确认的提醒（短信提醒），请您尽快进 行处理方案确认，以免造成订单交期影响。</p>
      <p class=" cl_666 mb24">（2）顾客对收到的产品存在投诉或不满，请通过系统客诉管理发起客诉，我们的工作人员 将会尽快与您取得联系，如情况紧急请直接电话联系您的专属客服。客诉过程双方应本着共 赢互利原则进行沟通处理，我们将尽可能地为顾客提供满意的服务。如涉及赔款，赔款金额 不超过本异常批次的合同原值。</p>
      <p class=" cl_666 mb24">（3）顾客在收到货物后请及时在系统中进行收货确认处理，从供方发货时间开始计算，超 过 10 天没有进行收货确认，系统将默认完成收货确认处理。</p>
      <p class="cl_333 mb24">5、发票管理</p>
      <p class=" cl_666 mb24">（1）下单时允许顾客自行选择是否有发票需要。</p>
      <p class=" cl_666 mb24">（2）普票：开普票金额需累计满 200 元；专票：开专票金额需累计满 1000 元； 由于样品金额小、款数多，所以平台是累积开票，如果普票金额不满 200 元/专票金额不满 1000 元，需到月底 20-25 号在后台提交开票资料，财务统一开.</p>
      <p class=" cl_666 mb24">（3）发票是不随货发出的，需要您收到货后，先确认收货，再在会员中心申请开票。</p>
      <p class="cl_333 mb24">6、注意事项</p>
      <p class=" cl_666 mb24">（1）系统下单时请务必保管好您个人帐号、密码、回单等资料，建议勿在网吧等公共场所 使用本下单系统。</p>
      <p class=" cl_666 mb24">（2）顾客应按照系统提示认真进行下单操作，因顾客个人原因造成的损失由顾客自行承担。</p>
      <p class=" cl_666 mb24">（3）交易过程中如发现已被扣款，但系统未显示成功支付，请及时联系客服人员进行核实 处理。</p>
      <p class="cl_333 mb24"></p>
      <p class=" cl_666 mb24">（1）在不抵触适用法律的最大范围内，捷配既不保证此网站不受干扰及没有错误，亦不保 证此网站或提供此网站的服务器没有病毒、感染或其它有害物质。捷配不会就使用此网站、 使用链接此网站的任何网站或使用经此等网站提供的任何产品或服务而产生的任何特定结 果作出任何担保或保证。</p>
      <p class=" cl_666 mb24">（2）捷配有权在必要时修改本服务条款以及各单项服务的相关条款。顾客在使用时，应当 及时查阅了解相关内容并自觉遵守。</p>
      <p class=" cl_666 mb24">（3）捷配订单系统保留随时修改或中断服务而不需通知顾客的权利。</p>
      <p class=" cl_666 ">（4）本服务条款的最终解释权在法律法规的规定内归捷配所有。</p>
    </div>
    <div class="both filx">
      <router-link :to="{path:'/NewUser',query: {elect: 0}}">不同意</router-link>
      <router-link :to="{path:'/NewUser',query: {elect: 1}}">同意</router-link>
    </div>
  </div>

</template>

<script>
  import Vue from "vue"
  import HomePageTop from "../common/HomePageTop";

  export default {
    name: "Contains",
    components: {HomePageTop},
    data() {
      return {
        //像子组件传值
        TopTotle:{name:"捷配注册协议",left:true,class:true},

      }
    },
    methods: {},
    created() {

    }
  }

</script>

<style>
  html, body {
    background: #fff;
  }
  .Contains .filx a {
    float: left;
    width: 50%;
    font-size: 0.34rem;
    text-align: center;
    color: #f90;
  }
  .Contains .filx a:first-child {
    float: left;
    width: 50%;
    border-right: 1px solid #D1D3D8;
    color: #999;
  }

</style>



